import axios from 'axios'

const baseUrl = '/api/countries'
const getAll = async () => {
  const response = await axios.get(baseUrl)
  return response.data
}

const getWeather = async (params) => {
  const res = await axios.get(
    `/api/weather/lat/${params.location[0]}/lng/${params.location[1]}/unit/${params.unit}`
  )
  return res.data
}

const getTimeZone = async (params) => {
  const res = await axios.get(
    `/api/time/lat/${params.location[0]}/lng/${params.location[1]}`
  )
  return res.data
}

const getCiaData = async (params) => {
  const response = await axios
    .get(`/api/ciadata/${params} `)
    .then((res) => {
      if (res.data === '{ "no data" }\n') {
        return { ...res, data: 'no data' }
      }
      return res
    })
    .catch((err) => {
      return err
    })
  return response
}

export default { getAll, getWeather, getTimeZone, getCiaData }
